import { Component, OnInit, ChangeDetectionStrategy, ViewChild, ChangeDetectorRef } from "@angular/core";

import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { AuthService } from "../shared/user/auth.service";
import { RoleService } from "../shared/role/role.service";
import { TeamService } from "../shared/team/team.service";
import { Template, TemplateField } from "../shared/template/template";
import { TemplateService } from "../shared/template/template.service";

@Component({
  selector: "app-template",
  templateUrl: "./template.component.html",
  styleUrls: ["./template.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TemplateComponent implements OnInit {
  @ViewChild("templateDetailsModal") templateDetailsModal;
  @ViewChild("createTemplateModal") createTemplateModal;
  @ViewChild("promptDeleteModal") promptDeleteModal;

  public templates: BehaviorSubject<Array<Template>>;
  public templateCount: number;
  public searchQuery: string;
  public templateForDelete: Template;
  public templateForCreate: Template;

  public step1: boolean;
  public step2: boolean;
  public step3: boolean;

  public templateFieldType: Array<{ value: string; label: string }>;
  public validationTypes: Array<{ value: string; label: string }>;
  public assignmentModes: Array<{ value: string; label: string }>;

  public roles: BehaviorSubject<Array<{ value: string; label: string }>>;
  public teams: BehaviorSubject<Array<{ value: string; label: string }>>;
  public currencies: Array<{ value: string; label: string }>;

  public step2Form: Array<TemplateField>;
  public step3Form: Array<TemplateField>;

  constructor(
    private authService: AuthService,
    public templateService: TemplateService,
    private roleService: RoleService,
    private teamService: TeamService,
    private ref: ChangeDetectorRef
  ) {
    this.templateForCreate = new Template();
    this.templateForDelete = new Template();

    this.step2Form = [new TemplateField()];
    this.step3Form = [new TemplateField()];

    this.step1 = false;
    this.step2 = true;
    this.step3 = true;

    this.roles = new BehaviorSubject([]);
    this.teams = new BehaviorSubject([]);
  }

  ngOnInit() {
    this.templateService.getTemplates().subscribe();
    this.templates = this.templateService.templates;
    this.templates.subscribe(data => {
      this.templateCount = data.length;
    });

    this.templateFieldType = [
      // NOTE: image must remain the last value in this array because it's being sliced out of the array in the view
      { value: "dropdown", label: "Dropdown" },
      { value: "text", label: "Text" },
      { value: "date", label: "Date" },
      { value: "time", label: "Time" },
      { value: "barcode", label: "Barcode" },
      { value: "image", label: "Image" }
    ];

    this.assignmentModes = [
      { value: "Broadcast", label: "Broadcast Assignment" },
      { value: "Auto", label: "Auto Assignment" },
      { value: "Manual", label: "Manual Assignment" },
      { value: "Team", label: "Team Assignment" }
      // { value: "UserGroup", label: "UserGroup" }
    ];

    this.roleService.roles.subscribe(data => {
      this.roles.next(
        data.map(role => {
          return { value: role.userGroupId, label: role.name };
        })
      );
    });

    this.teamService.teams.subscribe(data => {
      this.teams.next(
        data.map(team => {
          return { value: team.teamId, label: team.teamName };
        })
      );
    });

    this.currencies = [{ value: "USD", label: "USD" }, { value: "NGN", label: "NGN" }];

    this.validationTypes = [
      { value: "none", label: "None" },
      { value: "required", label: "Required" },
      { value: "validateMomAndPopDetails", label: "Mom And Pop Details" },
      { value: "validateCustomerDetails", label: "Customer Details" }
    ];
  }

  valueToLabel(value) {
    switch (value) {
      case "none":
        return "None";
      case "required":
        return "Required";
      case "validateMomAndPopDetails":
        return "Mom And Pop Details";
      case "validateCustomerDetails":
        return "Customer Details";
      default:
        return;
    }
  }

  searchValueChange(q) {
    this.searchQuery = q;
    this.templateService.searchTerm.next(this.searchQuery);
  }

  promptDeleteTemplate(template: Template) {
    this.templateForDelete = Object.assign(new Template(), template);
    this.promptDeleteModal.show();
  }

  deleteTemplate(template: Template) {
    this.templateService.deleteTemplate(Object.assign(new Template(), template));
    this.promptDeleteModal.hide();
    this.templateDetailsModal.hide();
  }

  startTemplateDetailsProcess() {
    this.step1 = false;
    this.step2 = true;
    this.step3 = true;

    this.step2Form = [new TemplateField()];
    this.step3Form = [new TemplateField()];

    this.templateDetailsModal.show();
  }

  showTemplateDetails(template: Template) {
    if (template.price == 0) {
      template.price = null;
    }
    this.templateForCreate = template;
    this.startTemplateDetailsProcess();
    const priorTemplate = template.getPriorTemplate();
    const mainTemplate = template.getTemplate();

    this.step2Form = priorTemplate.length ? priorTemplate : [new TemplateField()];
    this.step3Form = mainTemplate.length ? mainTemplate : [new TemplateField()];
  }

  newTemplateFormEntry(id) {
    if (id === 1) {
      this.step2Form.push(new TemplateField());
    } else if (id === 2) {
      this.step3Form.push(new TemplateField());
    }

    this.ref.detectChanges();
  }

  removeTemplateFormEntry(id, index) {
    if (id === 1) {
      this.step2Form.splice(index, 1);
    } else if (id === 2) {
      this.step3Form.splice(index, 1);
    }

    this.ref.detectChanges();
  }

  nextTab(id) {
    if (id === 1) {
      this.step1 = false;
      this.step2 = true;
      this.step3 = true;
    } else if (id === 2) {
      this.step1 = true;
      this.step2 = false;
      this.step3 = true;
    } else if (id === 3) {
      this.step1 = true;
      this.step2 = true;
      this.step3 = false;
    }
  }

  submitTemplate() {
    this.templateForCreate.setPriorTemplate(this.step2Form);
    this.templateForCreate.setTemplate(this.step3Form);
    if (!this.templateForCreate.price || this.templateForCreate.price <= 0) {
      this.templateForCreate.price = 0;
    }
    const userId = this.authService.getUserId();
    for (const i in this.templateForCreate) {
      if (this.templateForCreate.userId !== userId) {
        this.templateForCreate.userId = userId;
      }
    }

    this.templateForCreate.userId = this.templateForCreate.userId ? this.templateForCreate.userId : userId;

    this.templateService.createOrUpdateTemplate(Object.assign(new Template(), this.templateForCreate));
    this.createTemplateModal.hide();
  }

  openCreateTemplate() {
    this.templateForCreate = new Template();

    this.startTemplateCreateProcess();
  }

  startTemplateCreateProcess() {
    this.step1 = false;
    this.step2 = true;
    this.step3 = true;

    this.step2Form = [new TemplateField()];
    this.step3Form = [new TemplateField()];

    this.createTemplateModal.show();
  }

  editTemplate(template: Template) {
    this.templateForCreate = template;

    this.startTemplateCreateProcess();

    const priorTemplate = template.getPriorTemplate();
    const mainTemplate = template.getTemplate();

    this.step2Form = priorTemplate.length ? priorTemplate : [new TemplateField()];
    this.step3Form = mainTemplate.length ? mainTemplate : [new TemplateField()];
  }

  getTeamsForTemplate(template: Template) {
    const teamList = template.teams
      .map(teamId => {
        return this.teamService.teams.value
          .filter(t => {
            return t.teamId === teamId;
          })
          .map(t => {
            return t.teamName;
          })
          .join("");
      })
      .join(", ");

    if (teamList.length) {
      return teamList;
    } else {
      return "N/A";
    }
  }

  getUserGroupsForTemplate(template: Template) {
    const userGroups = template.userGroup
      .map(userGroupId => {
        return this.roleService.roles.value
          .filter(r => {
            return r.userGroupId === userGroupId;
          })
          .map(r => {
            return r.name;
          })
          .join("");
      })
      .join(", ");

    if (userGroups.length) {
      return userGroups;
    } else {
      return "N/A";
    }
  }
}



